// å¯¼å¥é¢è²éæ©å¨ç»ä»¶
import nkalive from './nkalive'
import store from './store/base/index';
import { UPDATE_KEEP_ALIVE_INCLUDES } from './store/base/mutation-types';

// å­å¨ç»ä»¶åè¡¨
const components = [
  nkalive
]


// å®ä¹ install æ¹æ³ï¼æ¥æ¶ Vue ä½ä¸ºåæ°ãå¦æä½¿ç¨ use æ³¨åæä»¶ï¼åææçç»ä»¶é½å°è¢«æ³¨å
const install = function (Vue,option) {
  // å¼åèå®ä¹çä»åºå
  Vue.prototype.$storeModulesName = option.storeModulesName || 'NKeepAlive'
  
  let routerArr = []
  const includesArr = option.includes
  /**
   * @param {ç¸å¯¹è·¯å¾} path
   * @description å¤æ­æ¯å¦å¨éç½®ä¸­
   */
  function isIncludes(path) {
    return includesArr.some(item => item.path === path)
  }
  
  // è·¯ç±åç½®å®å«
  option.App.router.beforeEach((to, from, next) => {
    // console.log('beforeEach');
    const lastRouterItem = routerArr[routerArr.length - 1] || {}
    if (to.fullPath === lastRouterItem.fullPath) { // è¿å
      if (isIncludes(from.path)) {
        option.App.store.commit(
          UPDATE_KEEP_ALIVE_INCLUDES,
          {
            componentName: includesArr.find(item => item.path === from.path).name,
            open: false,
          },
        )
      }
    } else { // åè¿
      routerArr.push(from)
      if (isIncludes(to.path)) {
        if (routerArr.some(item => item.fullPath === to.fullPath)) { // å­å¨åæ°åè·¯ç±ä¸æ ·
        }
        // eslint-disable-next-line
        if (routerArr.some(item => (item.path === to.path) && (item.fullPath !== to.fullPath))) { // å­å¨è·¯ç±ä¸æ ·ï¼åæ°ä¸ä¸æ ·ç
          option.App.store.commit(
            UPDATE_KEEP_ALIVE_INCLUDES,
            {
              componentName: includesArr.find(item => item.path === to.path).name,
              open: false,
            },
          )
        }
      }
    }
    next()
  })
  // è·¯ç±åç½®å®å«
  option.App.router.afterEach((to, from) => {
    // console.log('afterEach');
    const lastRouterItem = routerArr[routerArr.length - 1] || {}
    if (to.fullPath === lastRouterItem.fullPath) { // è¿å
      // å é¤æåä¸ä¸ªè®°å½
      routerArr = routerArr.splice(routerArr.length - 1, 1)
    } else { // åè¿
      routerArr.push(from)
      if (isIncludes(to.path)) {
        option.App.store.commit(
          UPDATE_KEEP_ALIVE_INCLUDES,
          {
            componentName: includesArr.find(item => item.path === to.path).name,
            open: true,
          },
        )
      }
    }
  })

  
  // å¤æ­æ¯å¦å¯ä»¥å®è£
  if (install.installed) return
  // éåæ³¨åå¨å±ç»ä»¶
  components.map(component => Vue.component(component.name, component))
}

// å¤æ­æ¯å¦æ¯ç´æ¥å¼å¥æä»¶
if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue)
}

export default {
  // å¯¼åºçå¯¹è±¡å¿é¡»å·æ installï¼æè½è¢« Vue.use() æ¹æ³å®è£
  install,
  // ä»¥ä¸æ¯å·ä½çç»ä»¶åè¡¨
  nkalive,
  // æä»¶ç¬æä»åº
  store,
}